@charset "UTF-8";
@import "partials/reset.css";
@import "partials/fonts.css";
@import "partials/select2.css";
@import "partials/jquery.modal.min.css";
/* my style */
a {
  text-decoration: none;
  color: #109cf1;
}

html,
body {
  max-width: 100%;
  height: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #334d6e;
}

::placeholder {
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  letter-spacing: 0.01em;
  color: #90a0b7;
}

::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

*:focus {
  outline: none;
}

.hide {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #334d6e;
}

.main {
  display: flex;
  min-height: 100%;
}
.main__content {
  flex-grow: 1;
  background: #e5e5e5;
  display: flex;
  flex-direction: column;
}

.container {
  padding: 0 36px;
  margin: 0 auto;
  box-sizing: border-box;
  width: 100%;
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  padding: 5px 29px;
  background: #00c2ff;
  border-radius: 4px;
  height: 40px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  transition: 0.3s;
  white-space: nowrap;
}
.btn:hover {
  background: #109cf1;
  box-shadow: 0px 4px 10px rgba(16, 156, 241, 0.24);
}
.btn__icon {
  flex-shrink: 0;
  margin-right: 8px;
}
.btn--bold {
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.01em;
}
.btn--large {
  min-width: 190px;
}
.btn--xlarge {
  min-width: 230px;
}
.btn--flex {
  display: flex;
  justify-content: center;
}
.btn--flex > * {
  align-self: center;
}

.checkbox input {
  display: none;
}
.checkbox span {
  width: 16px;
  height: 16px;
  border: 2px solid #d5d5d5;
  box-sizing: border-box;
  border-radius: 4px;
  display: block;
  cursor: pointer;
  position: relative;
}
.checkbox input:checked + span {
  background: #109cf1;
  border-color: #109cf1;
}
.checkbox input:checked + span::after {
  content: "";
  display: block;
  width: 10px;
  height: 8px;
  background: url("../img/checkbox.svg") center center no-repeat;
  background-size: contain;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.checkbox-big {
  display: flex;
}
.checkbox-big input {
  display: none;
}
.checkbox-big span {
  box-sizing: border-box;
  cursor: pointer;
  position: relative;
  top: 6px;
  width: 20px;
  height: 20px;
  border: 1px solid #b9bec7;
  border-radius: 0px;
  display: inline-block;
  flex-shrink: 0;
  margin-right: 12px;
}
.checkbox-big input:checked + span {
  background: #109cf1;
  border-color: #109cf1;
}
.checkbox-big input:checked + span::after {
  content: "";
  display: block;
  width: 10px;
  height: 8px;
  background: url("../img/checkbox.svg") center center no-repeat;
  background-size: contain;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.custom-radio {
  display: flex;
  flex-wrap: wrap;
  margin-top: -16px;
  margin-left: -16px;
}
.custom-radio__item {
  margin-top: 16px;
  margin-left: 16px;
}
@media (max-width: 767px) {
  .custom-radio__item {
    width: calc(50% - 16px);
  }
}
.custom-radio__item--third {
  width: calc(33.33% - 16px);
}
@media (max-width: 767px) {
  .custom-radio__item--third {
    width: calc(50% - 16px);
  }
}
@media (max-width: 767px) {
  .custom-radio__item--icon {
    width: 100%;
  }
}
.custom-radio__item-icon {
  display: flex;
  width: 24px;
  height: 24px;
  margin-right: 12px;
  margin-left: -8px;
  justify-content: center;
  align-self: center;
}
.custom-radio__item-icon svg {
  display: block;
  fill: #b0b4bd;
  align-self: center;
  transition: fill 0.3s ease;
}
.custom-radio__item-text {
  display: flex;
  justify-content: center;
  padding: 11px 24px;
  border: solid 1px #dadce0;
  font-size: 14px;
  line-height: 24px;
  border-radius: 3px;
  color: #334d6e;
  cursor: pointer;
  transition: border-color 0.3s ease, color 0.3s ease;
}
.custom-radio__item-wrapper {
  align-self: center;
  margin-top: -3px;
  margin-bottom: -3px;
  padding-bottom: 2px;
}
.custom-radio__item-wrapper span {
  line-height: 1;
}
.custom-radio__item-wrapper span.hint {
  display: block;
  font-size: 12px;
  color: #b0b4bd !important;
}
.custom-radio__item:not(.custom-radio__item--disabled):hover .custom-radio__item-text {
  border-color: #00c2ff;
  color: #00c2ff;
}
.custom-radio__item:not(.custom-radio__item--disabled):hover .custom-radio__item-icon svg {
  fill: #00c2ff;
}
.custom-radio__item--disabled {
  cursor: default;
}
.custom-radio__item--disabled .custom-radio__item-text {
  border-color: #C2CFE0;
  cursor: default;
  color: #C2CFE0;
}
.custom-radio__item--disabled .custom-radio__item-text span.hint {
  color: #C2CFE0 !important;
}
.custom-radio__item--disabled .custom-radio__item-text svg {
  fill: #C2CFE0;
}
input:checked ~ .custom-radio__item-text {
  border-color: #00c2ff;
  color: #00c2ff;
}
input:checked ~ .custom-radio__item-text .custom-radio__item-icon svg {
  fill: #00c2ff;
}

.bgoverflow {
  display: none;
}
@media (max-width: 999px) {
  .bgoverflow {
    transition: 0.3s;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vh;
    height: 100vh;
    z-index: 99;
    opacity: 0.6;
    display: none;
    background: #000000;
  }
  .bgoverflow.active {
    display: block;
  }
}

.sidebar {
  width: 256px;
  flex-shrink: 0;
  background: #ffffff;
  box-shadow: 6px 0px 18px rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  z-index: 10;
}
@media (max-width: 999px) {
  .sidebar {
    transition: 0.3s;
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    z-index: 100;
    transform: translateX(-100%);
    opacity: 0;
  }
  .sidebar.active {
    transform: translateX(0);
    opacity: 1;
  }
}
.sidebar__container {
  padding: 0 24px;
  box-sizing: border-box;
  width: 100%;
}
.sidebar__top {
  height: 60px;
  box-sizing: border-box;
  border-bottom: 1px solid #ebeff2;
  display: flex;
  align-items: center;
}
.sidebar__logo {
  display: block;
  max-width: 100%;
  width: 100%;
}
.sidebar__logo img {
  display: block;
  width: 100%;
  height: auto;
}
.sidebar__bottom {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.sidebar__userinfo {
  padding-top: 17px;
}
.sidebar__userinfo-name {
  font-weight: 500;
}
.sidebar__userinfo-email {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.01em;
  color: #707683;
  margin-top: 2px;
}
.sidebar__menu {
  padding: 25px 0;
}
.sidebar__login-menu {
  flex-grow: 1;
  box-sizing: border-box;
  padding-top: 25px;
  border-top: 1px solid #ebeff2;
}
.sidebar__footer {
  padding: 25px 0;
  position: relative;
}
.sidebar__footer-price {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #FFFFFF;
  background: #334D6E;
  padding: 10px 0;
}
@media (max-width: 999px) {
  .sidebar__footer-price {
    display: none !important;
  }
}
.sidebar__copy {
  color: #707683;
}

.side-menu__item + .side-menu__item {
  margin-top: 19px;
}
.side-menu__item svg {
  margin-right: 14px;
  flex-shrink: 0;
}
.side-menu__item a {
  display: flex;
  align-items: center;
  color: #c2cfe0;
}
.side-menu__item a span {
  color: #334d6e;
}
.side-menu__item:hover a, .side-menu__item--current a {
  color: #00c2ff;
}
.side-menu__item:hover a span, .side-menu__item--current a span {
  color: #00c2ff;
}

.topbar {
  height: 60px;
  box-sizing: border-box;
  border-bottom: 1px solid #ebeff2;
  background: #ffffff;
}
@media (max-width: 767px) {
  .topbar {
    height: 56px;
  }
}
.topbar__container {
  height: 100%;
}
.topbar__inner {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.topbar__mobile {
  display: none;
}
@media (max-width: 999px) {
  .topbar__mobile {
    display: flex;
    align-items: center;
  }
}
@media (max-width: 999px) {
  .topbar__menu {
    display: block;
    width: 24px;
    height: 24px;
    background: url("../img/burger.svg") center center no-repeat;
    background-size: contain;
    margin-right: 17px;
    cursor: pointer;
  }
}
@media (max-width: 999px) {
  .topbar__logo {
    display: block;
    width: 144px;
    margin-right: 30px;
  }
  .topbar__logo img {
    width: 100%;
    display: block;
  }
}
.topbar__search {
  flex-grow: 1;
}
@media (max-width: 767px) {
  .topbar__search {
    display: none;
  }
}
.topbar__search form {
  width: 100%;
}
.topbar__search input {
  padding-left: 32px;
  background: url("../img/icon-search.svg") left center no-repeat;
  width: 100%;
  box-sizing: border-box;
}
.topbar__notifications {
  margin-left: 20px;
}
.topbar__notifications-link {
  display: block;
  width: 17px;
  height: 20px;
  background: url("../img/icon-not.svg") center center no-repeat;
  background-size: contain;
  cursor: pointer;
  position: relative;
}
.topbar__notifications-link.has-new::after {
  content: "";
  background: #f7685b;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  display: block;
  position: absolute;
  right: -1px;
  top: 0;
  border-radius: 50%;
}
.topbar__info {
  width: 100%;
}
.topbar__info-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  max-width: 730px;
}
.topbar__balans {
  font-size: 14px;
  line-height: 21px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #00C2FF;
  border: 1px solid #B0B4BD;
  box-sizing: border-box;
  border-radius: 3px;
  padding: 6px 8px;
}
@media (max-width: 767px) {
  .topbar__balans {
    padding: 6px;
    line-height: 1.2;
  }
}
.topbar__balans + .topbar__balans {
  margin-left: 16px;
}
@media (max-width: 767px) {
  .topbar__hide {
    display: none;
  }
}

.footer {
  flex-shrink: 0;
}
.footer__copy {
  border-top: 1px solid #dadce0;
  padding: 32px 0 20px;
  color: #707683;
}

.page {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  max-height: calc(100% - 60px);
  height: calc(100vh - 60px);
  overflow: auto;
}
@media (max-width: 767px) {
  .page {
    max-height: none;
    height: auto;
  }
}
.page__container {
  padding-top: 32px;
  padding-bottom: 32px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .page__container {
    padding-top: 16px;
    padding-bottom: 0px;
  }
}
.page__title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 22px;
}
@media (max-width: 767px) {
  .page__title {
    display: block;
    margin-bottom: 13px;
  }
}
.page__title-h1 {
  font-size: 28px;
  line-height: 42px;
  letter-spacing: 0.01em;
}
@media (max-width: 767px) {
  .page__title-h1 {
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    width: 100%;
  }
}
.page__title-rigth {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .page__title-rigth {
    flex-direction: column-reverse;
    width: 100%;
    margin-top: 16px;
  }
}
.page__title-rigth .btn {
  padding-left: 24px;
  padding-right: 24px;
  /*height: 42px;
  width: 160px;*/
}
@media (max-width: 767px) {
  .page__title-rigth .btn {
    width: 100%;
  }
}

.sort-dir {
  display: flex;
  margin: 0 30px;
}
@media (max-width: 767px) {
  .sort-dir {
    width: 100%;
    margin: 24px 0 0;
    justify-content: space-between;
  }
}
.sort-dir__label {
  color: #6a707e;
}
.sort-dir__link {
  display: inline-block;
  margin-left: 0.3em;
  padding-right: 15px;
  background: url("../img/icon-sort.svg") right 4px center no-repeat;
}

.orders {
  background: #ffffff;
  border-radius: 8px;
  overflow: hidden;
}
@media (max-width: 767px) {
  .orders {
    border-radius: 0;
    margin-left: -20px;
    margin-right: -20px;
    box-shadow: 0px 0 5px rgba(0, 0, 0, 0.25);
  }
}

.order-date {
  display: flex;
  flex-wrap: wrap;
}
.order-date b {
  margin-right: 4px;
  font-weight: 500;
  color: #323c47;
}
.order-date span {
  font-weight: 500;
  color: #a6bf71;
}

.address-notice {
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
}
.address-notice svg {
  display: block;
}

.view-on-map {
  display: flex;
}
.view-on-map__icon {
  align-self: center;
  margin-right: 6px;
}
.view-on-map__icon svg {
  display: block;
}
.view-on-map__link {
  color: #2e90fe;
  font-weight: 500;
  text-decoration: underline;
}
.view-on-map__link:hover {
  text-decoration: none;
}

.orders-table {
  width: 100%;
  margin-top: -3px;
}
.orders-table__row {
  width: 100%;
  display: flex;
  padding-left: 32px;
  padding-right: 85px;
}
.orders-table__row--header {
  min-height: 54px;
  background: #dfe2ea;
}
@media (max-width: 767px) {
  .orders-table__row--header {
    display: none;
  }
}
.orders-table__row--header .orders-table__cell {
  color: #828282;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  font-weight: 500;
}
@media (max-width: 1240px) {
  .orders-table__row--header .orders-table__cell {
    font-size: 13px;
  }
}
.orders-table__row--item {
  position: relative;
  background: #f2f9ff;
  border-top: 1px solid #c8ceda;
  min-height: 63px;
  cursor: pointer;
}
.orders-table__row--item .orders-table__cell {
  letter-spacing: 0.01em;
  color: #707683;
}
.orders-table__row--item.active {
  background: #e3f2ff;
}
.orders-table__row--header, .orders-table__row--item {
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  padding-top: 5px;
  padding-bottom: 5px;
}
@media (max-width: 1240px) {
  .orders-table__row--header, .orders-table__row--item {
    padding-left: 20px;
    padding-right: 62px;
  }
}
@media (max-width: 767px) {
  .orders-table__row--header, .orders-table__row--item {
    padding: 15px 20px;
    flex-wrap: wrap;
  }
}
.orders-table__row--content {
  display: none;
  width: 100%;
  box-sizing: border-box;
  padding-right: 32px;
  padding-top: 10px;
  padding-bottom: 25px;
  box-shadow: inset 0px 7px 7px -7px rgba(0, 0, 0, 0.28), inset 0px -7px 7px -7px rgba(0, 0, 0, 0.28);
}
@media (max-width: 1240px) {
  .orders-table__row--content {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 767px) {
  .orders-table__row--content {
    padding: 24px 20px 28px;
  }
}
.orders-table__row--content.active {
  display: block;
}
.orders-table__cell {
  padding: 0 12px 0 0;
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .orders-table__cell {
    padding-right: 8px;
  }
}
@media (max-width: 480px) {
  .orders-table__cell {
    align-self: flex-start;
  }
  .orders-table__cell:nth-child(even) {
    padding-right: 0;
    text-align: right;
  }
}
.orders-table__cell--date {
  width: 14.341%;
}
@media (max-width: 767px) {
  .orders-table__cell--date {
    width: calc(50% - 50px);
    margin-bottom: 6px;
    order: -1;
  }
}
@media (max-width: 480px) {
  .orders-table__cell--date {
    width: 50%;
  }
}
.orders-table__cell--id {
  width: 15.3%;
}
@media (max-width: 767px) {
  .orders-table__cell--id {
    width: calc(50% - 50px);
    margin-bottom: 6px;
    order: -1;
  }
}
@media (max-width: 480px) {
  .orders-table__cell--id {
    width: 50%;
  }
}
.orders-table__cell--address {
  width: 23.8%;
}
@media (max-width: 767px) {
  .orders-table__cell--address {
    width: calc(50% - 50px);
  }
}
@media (max-width: 480px) {
  .orders-table__cell--address {
    width: 50%;
    margin-bottom: 6px;
  }
}
.orders-table__cell--status {
  flex-grow: 1;
}
@media (max-width: 767px) {
  .orders-table__cell--status {
    width: 100px;
    margin-bottom: 6px;
    padding-right: 0;
    order: -1;
    text-align: right;
  }
}
@media (max-width: 480px) {
  .orders-table__cell--status {
    width: 50%;
    order: 2;
  }
}
.orders-table__cell--price {
  width: 9.37%;
}
@media (max-width: 767px) {
  .orders-table__cell--price {
    width: 100px;
    padding-right: 0;
    text-align: right;
  }
}
@media (max-width: 480px) {
  .orders-table__cell--price {
    width: 50%;
    text-align: left !important;
    order: 1;
  }
}
.orders-table__edit {
  content: "";
  position: absolute;
  top: 50%;
  right: 55px;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background: url(../img/edit_order.svg) center center no-repeat;
  appearance: none;
  cursor: pointer;
}
.orders-table__edit:hover {
  filter: brightness(0);
}
@media (max-width: 1240px) {
  .orders-table__edit {
    right: 40px;
  }
}
@media (max-width: 767px) {
  .orders-table__edit {
    display: none;
  }
}
.orders-table__toggle {
  content: "";
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  padding: 0;
  border-radius: 4px;
  border: solid 1px #707683;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='11' height='7' viewBox='0 0 11 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='m0 0 5.145 4.18L10.29 0v1.83L5.145 6.01 0 1.83V0Z' fill='%23707683'/%3e%3c/svg%3e");
  appearance: none;
  cursor: pointer;
}
@media (max-width: 1240px) {
  .orders-table__toggle {
    right: 8px;
  }
}
@media (max-width: 767px) {
  .orders-table__toggle {
    display: none;
  }
}
.orders-table__toggle.active {
