.label-flex__checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
}
.label-flex__checkbox input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
  margin: 0;
  padding: 0;
  font-size: 0;
}
.label-flex__checkbox span {
  cursor: pointer;
  text-indent: -9999px;
  width: 37px;
  height: 20px;
  background: grey;
  display: block;
  border-radius: 20px;
  position: relative;
}
.label-flex__checkbox span:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 16px;
  transition: 0.3s;
}
.label-flex__checkbox input:checked + span {
  background: #0098D9;
}
.label-flex__checkbox input:checked + span:after {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}
.label-flex__checkbox label:active:after {
  width: 130px;
}
.label-flex__checkbox div {
  font-size: 14px;
  line-height: 150%;
  font-weight: normal;
  margin-left: 10px;
}

